.regMgSt_Body{
    width: 100%;
    height: 100dvh;
    overflow-y: scroll;
    position:fixed;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.regMgSt_content_Body{
    width: 90%;
    background-color: #fff;
    border-radius: 24px;
    padding: 3rem;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
}
.regMgSt_content_Body_box{
    width: 50%;
}
.regMgSt_nav{
    width: 90%;
    padding: 2rem;
    font-size: 1.5rem;
    font-weight: 500;
}
.regMgSt_content{
    display: flex;
    margin: 1rem;
}
.regMgSt_content_title , .regMgSt_content_value_box{
    width: 50%;
}
.regMgSt_content_value_box > input ,.regMgSt_content_value_box > select{
    border-radius: .4rem;
    padding:.6rem;
    box-sizing: border-box;
    border: 1px solid #B3B5B7;
}
.regMgSt_content_value{
    width: 100%;
}
.btn{
    padding:.3rem;
    background-color: black;
    color:#fff;
    border-radius: .3rem;
    border: none;
}
.crossLine{
    border:3px solid #F8F8F8;
}
.regMgSt_content_valueInButton{
    display: flex;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid #B3B5B7;
    padding:.3rem;
    color:#fff;
    border-radius: .3rem;
}
.regMgSt_content_valueInButton > .regMgSt_content_value{
    border:none;
}
.regMgSt_content_valueInButton .regMgSt_content_value:focus-visible {
    outline-color: #10FFB7;
}
.regMgSt_content_value_box > .regMgSt_content_value:focus-visible{
    outline-color: #10FFB7;
}
.regMgtSt_footer{
    width: 100%;
    display: flex;
    justify-content: center;
    margin: 2rem auto;
}
.regMgtSt_footer > div >.btn{
    cursor: pointer;
    padding: .7rem 1.4rem;
    margin: auto .3rem;
    border-radius: .5rem;
}
.regMgtSt_footer > div >.btn:hover{
    background-color: #B3B5B7;
}
.regMgtSt_footer > div>.btn:first-child:hover{
    background-color: #B3B5B7;
    color:#fff;
}
.regMgtSt_footer > div>.btn:first-child{
    background-color: #fff;
    color: #000;
    border:1px solid #B3B5B7;
}